___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --base-clr: #11121a;
  --line-clr: #42434a;
  --hover-clr: #222533;
  --text-clr: hsl(0, 0%, 98%);
  --accent-clr: #5e63ff;
  --secondary-text-clr: #b0b3c1;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;
}

body {
  min-height: 100vh;
  min-height: 100dvh;
  background-color: var(--base-clr);
  color: var(--text-clr);

  display: grid;
  grid-template-columns: auto 1fr;
}

.sidebar {
  height: 100vh;
  width: 250px;
  padding-block: 0.5rem;
  padding-inline: 1rem;
  background-color: var(--base-clr);
  border-right: 1px solid var(--line-clr);

  position: sticky;
  top: 0;
  align-self: start;

  transition: 300ms ease-in-out;
  overflow: hidden;
  text-wrap: nowrap;
}

.sidebar svg {
  width: 24px;
  height: 24px;
  flex-shrink: 0;
}

.toggle-btn svg {
  width: 20px;
  height: 20px;
  flex-shrink: 0;
}

.sidebar.close {
  padding: 5px;
  width: 60px;
}

.sidebar.close .logo {
  display: none !important;
}

.sidebar ul {
  list-style: none;
}

.sidebar > ul > a:first-child {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-block-end: 0.5rem;
}

.logo {
  font-weight: 600;
}

.sidebar ul li.active a {
  color: var(--accent-clr);

  svg {
    fill: var(--accent-clr);
  }
}

.sidebar a,
.sidebar .dropdown-btn {
  border-radius: 0.5rem;
  padding: 1rem;
  text-decoration: none;
  color: var(--text-clr);

  display: flex;
  align-items: center;
  gap: 1rem;
}

.dropdown-btn {
  width: 100%;
  text-align: left;
  background: none;
  border: none;
  font: inherit;
  cursor: pointer;
}

.sidebar svg {
  flex-shrink: 0;
  fill: var(--text-clr);
}

.sidebar a span,
.sidebar .dropdown-btn span {
  flex-grow: 1;
}

.sidebar a:hover,
.sidebar .dropdown-btn:hover {
  background-color: var(--hover-clr);
}

.sidebar .sub-menu {
  display: grid;
  grid-template-rows: 0fr;
  transition: 300ms ease-in-out;

  > div {
    overflow: hidden;
  }
}

.sidebar .sub-menu.show {
  grid-template-rows: 1fr;
}

.dropdown-btn svg {
  transition: 200ms ease;
}

.rotate svg:last-child {
  rotate: -180deg;
}

.sidebar .sub-menu a {
  padding-inline-start: 2rem;
}

.toggle-btn {
  margin-inline-start: 0;

  border: none;
  border-radius: 0.5rem;
  background: none;
  cursor: pointer;

  svg {
    transition: rotate 150ms ease;
  }
}

.toggle-btn:hover {
  background-color: var(--hover-clr);
}

main {
  padding: min(30px, 3%);
}

main p {
  color: var(--secondary-text-clr);
  margin-block-start: 0.5rem;
  margin-block-end: 1rem;
}

.container {
  border: 1px solid var(--line-clr);
  border-radius: 1rem;
  margin-bottom: 1.6rem;
  padding: min(24px, 15%);

  color: var(--text-clr);
}
